import { useLocation, useNavigate, useParams } from "react-router-dom";
import {
  useAdmissionPendingFeesOsStudentList,
  useAdmissionPendingFeesReminder,
} from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import {
  assestsUserFinanceUrl,
  assestsUtilityUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import CreateButton from "../../../../Ui/Button/CreateButton";
import FormTextarea from "../../../../Ui/Input/FormTextarea";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import PendingFeesSendReminderFilter from "./PendingFeesSendReminderFilter";
import PendingFeesSendReminderStudentList from "./PendingFeesSendReminderStudentList";
import { useTranslation } from "react-i18next";
import { useState } from "react";
import style from "../../../FinanceManager/FeesStructure/FeesStructure.module.css";
const PendingFeesSendReminder = ({ onClose, aid, fid, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [step, setStep] = useState(1);
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [sendNotify, setSendNotify] = useState({
    alarm_mode: "APP_NOTIFICATION",
    content: "",
  });
  const [studentList, setStudentList] = useState([]);
  const [selectedId, setSelectedId] = useState([]);

  const [applyFilter, setApplyFilter] = useState({
    depart: "",
    batch: "",
    master: "",
    batch_status: "",
    all_depart: "",
  });

  const [admissionPendingFeesOsStudentList] =
    useAdmissionPendingFeesOsStudentList();
  const [admissionPendingFeesReminder] = useAdmissionPendingFeesReminder();

  const onGetStudentList = () => {
    if (aid) {
      setDisabled((pre) => !pre);
      admissionPendingFeesOsStudentList({
        aid: aid,
        pendingData: {
          depart: applyFilter?.depart?._id ?? "",
          batch: applyFilter?.batch?._id ?? "",
          master: applyFilter?.master?._id ? [applyFilter?.master?._id] : [],
          batch_status: applyFilter?.batch_status ?? "",
          all_depart: applyFilter?.all_depart ?? "",
        },
      })
        .then((res) => {
          setStudentList(res?.data?.all_student);
          setDisabled((pre) => !pre);
          setStep(2);
        })
        .catch({});
    }
  };

  const onGrabStudent = (val) => {
    setSelectedId(val);
  };

  const onAction = () => {
    if (sendNotify.content) {
      if (aid && sendNotify.alarm_mode && selectedId?.length > 0) {
        setDisabled((pre) => !pre);
        admissionPendingFeesReminder({
          aid: aid,
          alarm_mode: sendNotify.alarm_mode,
          content: sendNotify.content,
          reminderData: {
            all_arr: selectedId,
            depart: applyFilter?.depart?._id ?? "",
            batch: applyFilter?.batch?._id ?? "",
            master: applyFilter?.master?._id ? [applyFilter?.master?._id] : [],
            batch_status: applyFilter?.batch_status ?? "",
            all_depart: applyFilter?.all_depart ?? "",
          },
        })
          .then(() => {
            setDisabled((pre) => !pre);
            onClose();
            navigate(
              `/q/${params.username}/member/staff/admission?type=pending&n=message`,
              {
                state: getQuery.state,
              }
            );
          })
          .catch({});
      }
    } else {
      setErrorField({
        query: "* required",
      });
    }
  };
  // console.info("instituteAllStudentIdList", instituteAllStudentIdList);
  return (
    <>
      {step === 1 ? (
        <PendingFeesSendReminderFilter
          applyFilter={applyFilter}
          setApplyFilter={setApplyFilter}
          onClose={onClose}
          fid={fid}
          onActionEventFire={onGetStudentList}
          instituteId={instituteId}
        />
      ) : step === 2 ? (
        <PendingFeesSendReminderStudentList
          alreadySelected={selectedId}
          studentList={studentList}
          onAction={onGrabStudent}
          onClose={() => setStep(1)}
        />
      ) : (
        <Modal>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("Notify_about_Outstanding_Dues")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={() => setStep(2)}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <section
                className={style.notify_container}
                onClick={() =>
                  setSendNotify((prev) => ({
                    ...prev,
                    alarm_mode: "APP_NOTIFICATION",
                  }))
                }
              >
                <img
                  src={
                    sendNotify.alarm_mode === "APP_NOTIFICATION"
                      ? `${assestsUtilityUrl}/radio-blue.svg`
                      : `${assestsUtilityUrl}/radio-grey.svg`
                  }
                  alt="icon"
                />
                <div className={style.notify_text}>
                  <h6>{t("Notify_with_In_app_Notification")}</h6>
                  <p>{t("Notify_with_In_app_Notification_note")}</p>
                </div>
              </section>
              <section
                className={style.notify_container}
                onClick={() =>
                  setSendNotify((prev) => ({
                    ...prev,
                    alarm_mode: "EMAIL_NOTIFICATION",
                  }))
                }
              >
                <img
                  src={
                    sendNotify.alarm_mode === "EMAIL_NOTIFICATION"
                      ? `${assestsUtilityUrl}/radio-blue.svg`
                      : `${assestsUtilityUrl}/radio-grey.svg`
                  }
                  alt="icon"
                />
                <div className={style.notify_text}>
                  <h6>{t("Notify_On_Email_and_SMS")}</h6>
                  <p>{t("Notify_On_Email_and_SMS_note")}</p>
                </div>
              </section>
              {sendNotify.alarm_mode === "EMAIL_NOTIFICATION" && (
                <FormTextarea
                  label={t("email_content")}
                  placeholder={t("email_content_placeholder")}
                  name="content"
                  value={sendNotify.content}
                  type="text"
                  onChange={(e) =>
                    setSendNotify((prev) => ({
                      ...prev,
                      content: e.target.value,
                    }))
                  }
                  errorShow={errorField["query"]}
                />
              )}
              <CreateButton label="confirm" onAction={onAction} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}

      {disabled && <QvipleLoader />}
    </>
  );
};

export default PendingFeesSendReminder;
